<template>
  <div>
    <a-badge dot>
      <a-button size="small" icon="el-icon-chat-round" @click="showDrawer" shape="round">
        <span class="hidden-sm-and-down"></span>
        <a-icon type="edit"/>
      </a-button>
    </a-badge>
    <el-drawer
        :modal="false"
        :visible.sync="isDrawerShow"
        :before-close="handleClose"
        :show-close="false"
        direction="rtl"
        size="70%">
      <el-button @click="isDrawerShow = false"></el-button>
      <iframe height="100%" src="https://course.nctu.edu.tw/"
              width="100%"/>
    </el-drawer>

  </div>
</template>
<script>

export default {
  name: 'course-select-system',
  data() {
    return {
      isDrawerShow: false,
      isWarned: false
    }
  },
  methods: {
    showDrawer() {
      if (this.isWarned) {
        this.isDrawerShow = true
        return
      }
      this.$alert(
          `<strong"></strong>
                     
                     <br>chrome<br>"SameSite by default cookies"Disabled/
`,
          '', {
            showCancelButton: true,
            showClose: false,
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning',
            dangerouslyUseHTMLString: true,
            showInput: true,
            inputValue: 'chrome://flags/#same-site-by-default-cookies'
          }).then(() => {
        this.isDrawerShow = true
        this.isWarned = true
      }).catch(() => {
        window.open(
            'https://medium.com/@azure820529/chrome-80-%E5%BE%8C%E9%87%9D%E5%B0%8D%E7%AC%AC%E4%B8%89%E6%96%B9-cookie-%E7%9A%84%E8%A6%8F%E5%89%87%E8%AA%BF%E6%95%B4-default-samesite-lax-aaba0bc785a3'// <- This is what makes it open in a new window.
        );
      });
    },
    handleClose() {
    }
  },
  computed: {}
}
</script>
