<template lang="pug">
  div
    el-alert.hidden-md-and-up(effect="dark" title="" type="warning")
    a-modal(title="NiceCoruseToU!" :visible="showAlert" ok-text="" cancel-text=""
      @ok="goToPage(true)" @cancel="goToPage(false)")
      p NiceCourseToUNCTU+
      p NiceCourseToUNiceCourseToU
      p 
    //a-alert(type="info" title="" show-icon close-text="" v-show="$store.state.user.hasPressedLike !== true" @close="goToPage(false)")
        span(slot="description")
            | NiceCoruseToU
            a(@click="goToPage(true)") 
    top-nav-bar
    dashboard.hidden-md-and-up(:md="24" :sm="24" :xs="24")

    a-layout
      //a-layout-sider(width="120" breakpoint="lg" collapsed-width="0" )
          a-menu(:default-selected-keys="['courseTable']" v-model="activeTab" @click="selectedTab" theme="dark" mode="inline")
              a-menu-item(key="courseTable")
                  a-icon(type="table")
                  span(style="font-size:8px") 
              a-menu-item(key="discuss")
                  a-icon(type="team")
                  span(style="font-size:8px") 
              a-menu-item(@click="goToPage(true)")
                  a-icon(type="facebook")
                  span(style="font-size:8px") FB
      a-layout(v-if="activeTab.includes('courseTable')")
        a-layout-sider.hidden-sm-and-down(width="33%")
          a-layout
            a-layout-content
              course-classified-list
            a-layout-footer
              dashboard
        a-layout-content(:xs="24" :sm="24" :md="15"  :lg="15" :xl="15" )
          //course-timetable
          course-timetable-rollback

    a-row
      a-col.hidden-md-and-up(:md="24" :sm="24" :xs="24")
        course-classified-list
</template>

<script>
import CourseTimetable from "@/components/Element/CourseTimetable";
import Dashboard from "@/components/Element/Dashboard";
import TopNavBar from "@/components/Outline/TopNavBar";
import CourseClassifiedList from "@/components/Outline/CourseClassifiedList";
import CourseTimetableRollback from "@/components/Element/CourseTimetableRollback";
import {saveSession} from "@/sessionSave";

export default {
  name: "CourseTutorMain",
  data() {
    return {
      activeTab: ["courseTable"],
      showAlert: false
    }
  },
  components: {
    CourseTimetableRollback,
    CourseClassifiedList,
    TopNavBar, Dashboard, CourseTimetable
  },
  methods: {
    goToPage(accept) {
      this.activeTab = ['courseTable']
      this.showAlert = false
      this.$store.state.user.hasPressedLike = true
      this.$store.state.user.hasWatchedRequest = true
      saveSession(this.$store.state)
      if (accept) {
        window.open('https://www.facebook.com/NiceCourseToU/')
      } else {
        this.$alert("", '', {
          confirmButtonText: '...',
          cancelButtonText: ''
        }).then(() =>
            window.open('https://www.facebook.com/NiceCourseToU/')
        ).catch(() =>
            window.open('https://www.facebook.com/NiceCourseToU/'))
      }
    },
    selectedTab() {
      console.log(this.activeTab)
    }
  },
  mounted() {
    if (this.$store.state.user.hasWatchedRequest !== true) {
      setTimeout(() => {
        this.showAlert = true
      }, 1000 * 25)
    }
  }
}
</script>

