<template>
    <div>
        <el-tabs :stretch="true" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="" name=""></el-tab-pane>
            <el-tab-pane label="" name=""></el-tab-pane>
            <el-tab-pane label="" name=""></el-tab-pane>
            <el-tab-pane label="" name=""></el-tab-pane>
            <el-tab-pane label="" name=""></el-tab-pane>
            <el-tab-pane label="" name=""></el-tab-pane>
            <el-tab-pane label="" name=""></el-tab-pane>
        </el-tabs>
        <el-button size="mini" @click="showSoonNotification"> </el-button>
        <el-row>
            <el-col :span="24">
                <course-list-simplified
                        :present-courses="presentCourses"/>
            </el-col>
        </el-row>

    </div>
</template>
<script>
    import {findDupNameCourses} from "@/courseProcessor";
    import CourseListSimplified from "@/components/Element/CourseListSimplified";

    export default {
        name: 'draw-course-selection',
        components: {CourseListSimplified},
        data() {
            return {
                activeName: ''
            };
        }
        ,
        methods: {
            handleClick(tab, event) {
                console.log(tab, event);
            },
            showSoonNotification() {
                this.$alert('', '', {
                    confirmButtonText: '9/7 ',
                })
            }
        },
        computed: {
            presentCourses() {

                const userDepAlias = this.user.dep.substr(0, 2);
                var courses = this.allCoursesList
                const filterOptions = {
                    '': (c) => c.type.includes(this.activeName),
                    '': (c) => c.brief.includes(this.activeName) && !c.cname.includes(''),
                    '': (c) => c.brief.includes(this.activeName),
                    '': (c) => c.cname.includes(''),
                    '': (c) => (c.cname === ('()') || c.cname === ('()')) && c.type.includes(''),
                    '': (c) => c.brief.includes(''),
                    '': (c) => c.dep_cname.includes(this.activeName) || c.memo.includes(this.activeName) || c.dep_cname.includes(userDepAlias),
                }
                courses = courses.filter(filterOptions[this.activeName])
                if (this.activeName === '') {
                    courses = courses.filter((c) => !((c.cname === ('()') || c.cname === ('()'))))
                    courses = courses.filter((c) => !(c.cname.includes('')))
                    courses = courses.filter((c) => !(c.cname.includes('')))
                    courses = courses.filter((c) => (c.dep_cname.includes(userDepAlias)))
                    courses = findDupNameCourses(courses).dupCourses
                }

                return courses
            }
        },
    }
</script>
